<template>
  <div>
    <loading :active.sync="isLoading">
      <!-- loading -->
      <h3 v-if="useSlot">
        <i class="fas fa-cog fa-spin" style="font-size:200px"></i>
      </h3>
    </loading>

    <div class="text-right mt-4">
      <button class="btn btn-primary" @click="openModal(true)"></button>
    </div>
    <table class="table mt-4">
      <thead>
        <th width="120"></th>
        <th></th>
        <th width="120"></th>
        <th width="120"></th>
        <th width="100"></th>
        <th width="200"></th>
      </thead>
      <tbody>
        <tr v-for="(items) in products" :key="items.id">
          <td>{{ items.category }}</td>
          <td>{{ items.title }}</td>
          <td class="text-right">{{ items.origin_price | currency }}</td>
          <td class="text-right">{{ items.price | currency }}</td>
          <td>
            <span v-if="items.is_enabled==1" class="text-success"></span>
            <!--  -->
            <span v-else></span>
          </td>

          <td class="d-flex">
            <button class="btn btn-outline-primary btn-sm" @click="openModal(false,items)"></button>
            <button class="btn btn-outline-danger btn-sm" @click="delModal(items)"></button>
          </td>
        </tr>
      </tbody>
    </table>

      <Pagination :pagination="pagination" @getPage="getProducts"></Pagination>

    <!--Modal-->
    <div
      class="modal fade"
      id="productsModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-4">
                <div class="form-group">
                  <label for="image"></label>
                  <input
                    type="text"
                    v-model="tempProducts.imageUrl"
                    class="form-control"
                    id="image"
                    placeholder=""
                  />
                </div>
                <div class="form-group">
                  <label for="customFile">
                     
                    <i class="fas fa-spinner fa-spin" v-if="status.fileUploading"></i>
                  </label>
                  <input
                    type="file"
                    id="customFile"
                    class="form-control"
                    ref="files"
                    @change="uploadFile"
                  />
                </div>
                <img :src="tempProducts.imageUrl" class="img-fluid" alt />
              </div>
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title"></label>
                  <input
                    type="text"
                    v-model="tempProducts.title"
                    class="form-control"
                    id="title"
                    placeholder=""
                  />
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="category"></label>
                    <input
                      type="text"
                      v-model="tempProducts.category"
                      class="form-control"
                      id="category"
                      placeholder=""
                    />
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input
                      type="unit"
                      v-model="tempProducts.unit"
                      class="form-control"
                      id="unit"
                      placeholder=""
                    />
                  </div>
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="origin_price"></label>
                    <input
                      type="number"
                      v-model="tempProducts.origin_price"
                      class="form-control"
                      id="origin_price"
                      placeholder=""
                    />
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input
                      type="number"
                      v-model="tempProducts.price"
                      class="form-control"
                      id="price"
                      placeholder=""
                    />
                  </div>
                </div>
                <hr />

                <div class="form-group">
                  <label for="description"></label>
                  <textarea
                    type="text"
                    v-model="tempProducts.description"
                    class="form-control"
                    id="description"
                    placeholder=""
                  ></textarea>
                </div>
                <div class="form-group">
                  <label for="content"></label>
                  <textarea
                    type="text"
                    v-model="tempProducts.content"
                    class="form-control"
                    id="content"
                    placeholder=""
                  ></textarea>
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <input
                      class="form-check-input"
                      type="checkbox"
                      v-model="tempProducts.is_enabled"
                      :true-value="1"
                      :false-value="0"
                      id="is_enabled"
                    />
                    <label class="form-check-label" for="is_enabled"></label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-primary" @click="updateProduct"></button>
          </div>
        </div>
      </div>
    </div>

    <!--Modal-->
    <div
      class="modal fade"
      id="delProductModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            
            <strong class="text-danger">{{ tempProducts.title }}</strong> ()
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-danger" @click="readydelModal"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
import $ from "jquery";
import Pagination from '../Pagination';

export default {
  data() {
    return {
      products: [],
      tempProducts: {}, //()APIposttempProducts
      isNew: false,
      isLoading: false,
      useSlot: true, //loading
      status: {
        fileUploading: false
      },
      pagination: {}
    };
  },

  components:{
    Pagination,
  },

  methods: {
    getProducts(page = 1) {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/products?page=${page}`;
      // console.log('API:'+process.env.APIPATH,'APIPath:'+process.env.CUSTOMPATH);
      const vm = this;
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        console.log(response.data);
        vm.isLoading = false;
        vm.products = response.data.products;
        vm.pagination = response.data.pagination;
      });
    },
    openModal(isNew, items) {
      if (isNew) {
        this.tempProducts = {};
        this.isNew = true;
      } else {
        this.tempProducts = Object.assign({}, items); //items  tempProductsObject.assign {}{}
        this.isNew = false;
      }
      $("#productsModal").modal("show");
    },
    updateProduct() {
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product`;
      // console.log('API:'+process.env.APIPATH,'APIPath:'+process.env.CUSTOMPATH);
      const vm = this;
      let httpMethod = "post";
      console.log("", vm.isNew);
      if (!vm.isNew) {
        //falsetrue
        api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProducts.id}`;
        httpMethod = "put";
      }
      this.$http[httpMethod](api, { data: vm.tempProducts }).then(response => {
        console.log(response.data);
        if (response.data.success) {
          $("#productsModal").modal("hide");
          vm.getProducts();
        } else {
          $("#productsModal").modal("hide");
          vm.getProducts();
          console.log("");
        }
      });
    },

    uploadFile() {
      console.log(this);
      const uploadedFile = this.$refs.files.files[0]; //
      const vm = this;
      // const id = this.$refs.files.id;
      vm.status.fileUploading = true;
      const formData = new FormData(); // FormData 
      formData.append("file-to-upload", uploadedFile); // FormData 
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/upload`;
      this.$http
        .post(url, formData, {
          headers: {
            "Content-Type": "multipart/form-data"
          } //form-data
        })
        .then(response => {
          console.log(response.data);
          vm.status.fileUploading = false;
          if (response.data.success) {
            // vm.tempProducts.imageUrl = response.data.imageUrl; // imageUrl   $set 
            vm.$set(vm.tempProducts, "imageUrl", response.data.imageUrl); //imageUrl:response.data.imageUrltempProduct
            // document.getElementById(id).value=""; 
          } else {
            this.$bus.$emit("message:push", response.data.message, "danger");
          }
        });
    },

    delModal(items) {
      console.log("this.tempProducts", this.tempProducts, "items", items);
      this.tempProducts = items; //tempProducts
      // console.log(this.tempProducts);
      $("#delProductModal").modal("show"); //
    },
    readydelModal() {
      let vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProducts.id}`;
      this.$http.delete(api).then(response => {
        console.log(response.data);
        if (response.data.success) {
          $("#delProductModal").modal("hide");
          vm.getProducts(); //
          console.log("");
        } else {
          $("#delProductModal").modal("hide");
          vm.getProducts();
          console.log("");
        }
      });
    }
  },

  created() {
    this.getProducts();
  }
};
</script>



