//

//
import Vue from 'vue';
import VueRouter from 'vue-router';

//
//manager
import Login from '@/components/manager/pages/Login';
import Dashboard from '@/components/manager/Dashboard';
import Products from '@/components/manager/pages/Products';
import Coupon from '@/components/manager/pages/Coupon';
import Customorder from '@/components/manager/pages/Customorder';
import Productslist from '@/components/manager/pages/Prolist';
import CustomCheckout from '@/components/manager/pages/CustomCheckout';
//customer
import CustomerDashboard from '@/components/customer/CustomerDashboard';
import Homeheader from '@/components/customer/Homeheader';
import AllProduct from '@/components/customer/pages/AllProduct';
import ShoppingCart from '@/components/customer/pages/ShoppingCart';
//checkout
import Checkout from '@/components/customer/Checkout';
import Cart from "@/components/customer/pages/Cart";
import Complete from "@/components/customer/pages/Complete";
import Formdata from "@/components/customer/pages/Formdata";
Vue.use(VueRouter); // Vue <router-view/>  <router-link/> 
//

export default new VueRouter({
    routes:[
    {
        path: '/home',
        name: 'home',
        component: Homeheader
    },
    {
        path:'*',    //*
        redirect:'/login' // redirect  
      
    },
    {
        name:'Login',//
        path:'/login',//
        component:Login,//
    },

    //customer

    {
        name:'customer_dashboard',
        path:'/store',
        component:CustomerDashboard,
        children:[
            {
                name:'AllProduct',
                path:'allproduct',
                component:AllProduct,
            },
            {
                name:'ShoppingCart',
                path:'shopping_cart/:itemId',
                component:ShoppingCart,
            },
        ]
    },

    //checkout 
    {
        name:'checkout',
        path:'/checkout',
        component: Checkout,
        children:[
        {
        name:'cart',
        path:'cart',
        component: Cart,
        },
        {
            name:'formdata',
            path:'formdata/:orderId',
            component: Formdata,
        },
        {
            name:'complete',
            path:'complete',
            component: Complete,
        },
    ]
    },




    //manager
    {
        name:'Dashboard',
        path:'/dashboard',
        component:Dashboard,
        children:[
            {
            name:'Products', //
            path:'products',
            component:Products,
            meta: { requiresAuth: true }
            },
            {
                name:'Products_list',  //
                path:'products_list',
                component:Productslist,
                meta: { requiresAuth: true }
                },
                {
                name:'Coupon',  //
                path:'coupon',
                component:Coupon,
                meta: { requiresAuth: true }
                },
        ]
        
    },

    {
        name:'admin',
        path:'/',
        component:Dashboard,
        children:[
            {
            name:'CustomOrder',
            path:'custom_order',
            component:Customorder,
            },
            {
                name:'CustomCheckout',
                path:'custom_checkout/:orderId', //
                component:CustomCheckout,
                }
        ]
        
    },
   
]
});




