// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from './App';
import './bus'; //event bus
import router from './router';
import axios from 'axios'; //Ajax
import VueAxios from 'vue-axios'; //Vue
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
import 'bootstrap'; //bootstrap  jQuery  popper
//import 'jquery';   ESLint 
// main.js  import $ from 'jquery' import $ from 'jquery' 
import currencyFilter from './filters/currency'; //
import  VeeValidate  from 'vee-validate'; //
import zhTW from 'vee-validate/dist/locale/zh_TW'; //
import VueI18n from 'vue-i18n';
Vue.use(VueI18n);

const i18n = new VueI18n({
  locale: 'zhTW'
});
Vue.use(VeeValidate, {
  events: 'input|blur', //
  i18n,
  dictionary: {
    zhTW
  }
});


Vue.component('Loading',Loading); //
Vue.use(VueAxios, axios) //  Vue (main.js)Vue.use
Vue.config.productionTip = false;
axios.defaults.withCredentials = true; //APIsectionCookie  Cookie
Vue.filter('currency', currencyFilter); //filter





/* eslint-disable no-new */
new Vue({
  i18n,
  el: '#app',
  components: { App },
  template: '<App/>',
  router,
})

router.beforeEach((to, from, next) => {  //router.beforeEach()
  console.log('to',to,'from',from,'next',next);

  if(to.meta.requiresAuth){   //login
    const api = `${process.env.APIPATH}/api/user/check`;
    axios.post(api).then(response => {
      console.log(response.data);
      if(response.data.success){
          // vm.$router.push('/');
          next();
      }else{
        next({
          path:'/login'
        });
      }
    });
    console.log('');
  }else{
    next(); //
  }

})





